import Vue from 'vue'
import Vuex from 'vuex'
import querystring from 'vux/src/tools/querystring/index.js'

Vue.use(Vuex)

const vux = {
	state: {
		isLoading: false,
		tabbar: false
	},
	mutations: {
		updateLoadingStatus (state, payload) {
			state.isLoading = payload.isLoading
		},
		setVersion (state, payload) {
			state.version = payload.version
		},
		hasTabbar (state, payload) {
			state.tabbar = payload.tabbar
		}
	}
}

const storex = {
	state: {
		url: '/app/index.php?' + 'i=' + querystring.parse().i + '&c=' + querystring.parse().c + '&m=' + querystring.parse().m + '&',
		payurl: '/app/index.php?' + 'i=' + querystring.parse().i + '&c=mc&a=cash&',
		userInfo: JSON.parse(sessionStorage.getItem('userInfo')) || {},
		storeInfo: JSON.parse(sessionStorage.getItem('storeInfo')) || {},
		error: ''
	},
	mutations: {
		setUserInfo (state, userInfo) {
			sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
			state.userInfo = userInfo
		},
		setStoreInfo (state, storeInfo) {
			sessionStorage.setItem('storeInfo', JSON.stringify(storeInfo))
			state.storeInfo = storeInfo
		},
		haveError (state, error) {
			state.error = error
		}
	}
}

export default new Vuex.Store({
	modules: {
		vux,
		storex
	}
})



