
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
export default {
	name: 'app',
	components: {
		ViewBox,
		Tabbar,
		TabbarItem,
		Loading
	},
	computed: {
		...mapState({
			route: state => state.route,
			path: state => state.route.path,
			isLoading: state => state.vux.isLoading,
			HasTabbar: state => state.vux.tabbar,
			reversedId: state => state.storex.storeInfo.id
		}),
		isIndex () {
			return /StoreIndex/.test(this.route.path)
		},
		isCategory () {
			return /Category/.test(this.route.path)
		},
		isHome () {
			return /Home/.test(this.route.path)
		},
		isReserve () {
			return /Reserve/.test(this.route.path)
		}
	},
	created() {
	}
}




