
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Swiper,
		SwiperItem,
		Flexbox,
		FlexboxItem,
		XButton
	},
	computed: {
		...mapState({
			storeInfo: state => state.storex.storeInfo,
			url: state => state.storex.url
		})
	},
	data() {
		return {
			goodsInfo: []
		}
	},
	created() {
		const self = this
		this.$http.get(this.url + '&do=goods&op=goods_info', {
				params: {
					id: self.$route.params.storeid,
					goodsid: self.$route.params.goodsid
				}
			})
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.goodsInfo = response.message
				}
			})
	}
}




