
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Group,
		Cell,
		XInput,
		Selector,
		PopupPicker,
		XAddress,
		Datetime,
		XNumber,
		XTextarea,
		Popup,
		Scroller,
		Radio
	},
	computed: {
		total: function() {
			return this.nums * this.goods_info.cprice
		},
		...mapState({
			storeInfo: state => state.storex.storeInfo,
			url: state => state.storex.url
		}),
		day: function() {
			const day = (Date.parse(this.hotel.etime) - Date.parse(this.hotel.btime)) / 86400 / 1000
			return day > 0 ? day : 0
		},
		startDate() {
			if (this.hotel.btime) {
				const day = (Date.parse(this.hotel.btime) / 1000 + 86400)
				return dateFormat(new Date(day * 1000), 'YYYY-MM-DD')
			} else {
				const day = (Date.parse(this.startDate1) / 1000 + 86400)
				return dateFormat(new Date(day * 1000), 'YYYY-MM-DD')
			}
		},
		endDate() {
			if (this.hotel.etime) {
				const day = (Date.parse(this.hotel.etime) / 1000) - 86400
				return dateFormat(new Date(day * 1000), 'YYYY-MM-DD')
			} else {
				return ''
			}
		}
	},
	methods: {
		showaddress() {
			this.showadd = true
			this.address_info = this.address_info
		},
		change(key) {
			console.log(key)
			this.showadd = false
		},
		post: function() {
			const self = this
			let order = ''
			if (this.isOrder) {
				order = this.hotel
			} else {
				order = this.order
			}
			order.nums = self.nums
			this.$http.post(this.url + 'do=goods&op=order&id=' + this.storeInfo.id + '&goodsid=' + this.goods_info.id + '&action=' + this.action, {
					order: order
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.$vux.toast.show({
							text: '正在生产订单'
						})
						setTimeout(function() {
							self.$router.push('/Pay/' + response.message)
						}, 2000)
					}
				})
		}
	},
	data() {
		return {
			isOrder: 0,
			action: (/Order/.test(this.$route.path)) ? 'reserve' : 'buy',
			hotel: {
				btime: '',
				etime: '',
				remark: '',
				contact_name: '',
				mobile: ''
			},
			nums: 1,
			showadd: false,
			goods_info: '',
			info: '',
			address_info: [],
			order: {
				mode_distribute: 1,
				order_time: '',
				addressid: '',
				contact_name: '',
				mobile: ''
			},
			address: '',
			startDate1: dateFormat(new Date(), 'YYYY-MM-DD')
		}
	},
	beforeCreate() {
		const self = this
		this.$http.get('/app/index.php?i=281&c=entry&do=goods&m=we7_storex&op=info', {
				params: {
					id: self.$route.params.storeid,
					goodsid: self.$route.params.goodsid
				}
			})
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.info = response.message.info
					self.goods_info = response.message.goods_info
					for (let item in response.message.address) {
						self.address_info.push({
							key: response.message.address[item].id,
							value: response.message.address[item].province + response.message.address[item].city + response.message.address[item].district + response.message.address[item].address
						})
					}
					self.isOrder = (self.goods_info.hotelid > 0)
				}
			})
	}
}




