
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import base64 from 'vux/src/tools/base64/index.js'

import { mapState } from 'vuex'
export default {
	components: {
		Group,
		Cell
	},
	computed: {
		...mapState({
			storeInfo: state => state.storex.storeInfo,
			url: state => state.storex.url,
			payurl: state => state.storex.payurl
		})
	},
	methods: {
		paywechat() {
			console.log('paywechat')
			console.log(this.url + '&do=orders&op=pay&id=' + this.$route.params.payid)
			location.href = this.payurl + 'do=credit&params=' + this.params
		}
	},
	data() {
		return {
			payinfo: '',
			orderinfo: ''
		}
	},
	created() {
		const self = this
		this.$http.get(this.url + '&do=orders&op=orderpay&id=' + this.$route.params.payid)
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.orderinfo = JSON.parse(response.message.params)
					self.payinfo = response.message.pay
					self.params = base64.encode(response.message.params)
				}
			})
	}
}




