// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import { sync } from 'vuex-router-sync'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(AjaxPlugin)
Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
import store from './store'

import HomeIndex from './components/Home/Index'
import UserInfo from './components/Home/UserInfo'
import UserOrder from './components/Home/OrderList'
import OrderInfo from './components/Home/OrderInfo'
import Address from './components/Home/Address'
import AddressEdit from './components/Home/AddressEdit'
import Credit from './components/Home/Credit'
import CreditsRecord from './components/Home/CreditsRecord'
import Store from './components/Store/Store'
import StoreList from './components/Store/StoreList'
import StoreIndex from './components/Store/StoreIndex'
import StoreDetail from './components/Store/StoreDetail'
import Category from './components/Store/Category'
import Reserve from './components/Store/Reserve'
import GoodList from './components/Store/GoodList'
import GoodInfo from './components/Store/GoodInfo'
import Buy from './components/Store/Buy'
import Pay from './components/Store/Pay'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		component: Store,
		children: [
			{
				path: '/Home',
				component: HomeIndex,
				meta: {
					pageTitle: '个人中心'
				}
			},
			{
				path: '/Home/UserInfo',
				component: UserInfo,
				meta: {
					pageTitle: '个人信息'
				}
			},
			{
				path: '/Home/OrderList',
				component: UserOrder,
				meta: {
					pageTitle: '订单列表'
				}
			},
			{
				path: '/Home/OrderInfo/:oid',
				component: OrderInfo,
				meta: {
					pageTitle: '订单信息'
				}
			},
			{
				path: '/Home/Address',
				component: Address,
				meta: {
					pageTitle: '地址信息'
				}
			},
			{
				path: '/Home/AddressEdit/:id',
				component: AddressEdit,
				meta: {
					pageTitle: '编辑地址'
				}
			},
			{
				path: '/Home/CreditsRecord',
				component: CreditsRecord,
				meta: {
					pageTitle: '余额记录'
				}
			},
			{
				path: '/Home/Credit',
				component: Credit,
				meta: {
					pageTitle: '余额'
				}
			},
			{
				path: '/:ss',
				component: StoreList,
				meta: {
					pageTitle: '店铺列表'
				}
			},
			{
				path: 'StoreIndex/:storeid',
				component: StoreIndex,
				meta: {
					pageTitle: '店铺首页'
				}
			},
			{
				path: 'StoreDetail/:storeid',
				component: StoreDetail,
				meta: {
					pageTitle: '店铺详情'
				}
			},
			{
				path: 'Category/:storeid',
				component: Category,
				meta: {
					pageTitle: '分类'
				}
			},
			{
				path: 'Reserve/:storeid',
				component: Reserve,
				meta: {
					pageTitle: '预约'
				}
			},
			{
				path: 'GoodList/:storeid/:sub_id',
				component: GoodList,
				meta: {
					pageTitle: '商品列表'
				}
			},
			{
				path: 'GoodInfo/:storeid/:goodsid',
				component: GoodInfo,
				meta: {
					pageTitle: '商品信息'
				}
			},
			{
				path: 'Buy/:storeid/:goodsid',
				component: Buy,
				meta: {
					pageTitle: '购买'
				}
			},
			{
				path: 'Order/:storeid/:goodsid',
				component: Buy,
				meta: {
					pageTitle: '预订'
				}
			},
			{
				path: 'Pay/:payid',
				component: Pay,
				meta: {
					pageTitle: '支付'
				}
			}
		]
	}
]

const router = new VueRouter({
	history: true,
	routes
})
sync(store, router)
FastClick.attach(document.body)

router.beforeEach(function (to, from, next) {
	store.commit('hasTabbar', {tabbar: false})
	store.commit('updateLoadingStatus', {isLoading: true})
	var userInfo = JSON.parse(sessionStorage.getItem('userInfo')) || ''
	let storeInfo = JSON.parse(sessionStorage.getItem('storeInfo')) || {}
	if (to.params.storeid && (storeInfo.id !== to.params.storeid)) {
		AjaxPlugin.$http.get('/app/index.php?i=281&c=entry&do=store&m=we7_storex&op=store_detail', {
			params: {
				store_id: to.params.storeid
			}
		})
		.then(function (response) {
			response = response.data.message
			if (response.errno) {
				store.commit('setStoreInfo', response.message)
			} else {
				store.commit('setStoreInfo', response.message)
			}
		})
	} else {
		store.commit('setStoreInfo', storeInfo)
	}
	if (!userInfo) {
		console.log('1')
		AjaxPlugin.$http.get('/app/index.php?i=281&c=entry&do=usercenter&m=we7_storex&op=personal_info')
			.then(function (response) {
				response = response.data.message
				if (response.errno) {
					store.commit('setStoreInfo', response.message)
				} else {
					store.commit('setUserInfo', response.message)
				}
			})
	} else {
		store.commit('setUserInfo', userInfo)
	}
	next()
})

router.afterEach(function (to) {
	setTimeout(function () {
		store.commit('updateLoadingStatus', {isLoading: false})
	})
	typeof to.meta.pageTitle !== undefined && setDocumentTitle(to.meta.pageTitle)
})

let setDocumentTitle = function (title) {
	document.title = title
	let ua = navigator.userAgent
	if (ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
		var i = document.createElement('iframe')
		i.src = '/favicon.ico'
		i.style.display = 'none'
		i.onload = function () {
			setTimeout(function () {
				i.remove()
			}, 9)
		}
		document.body.appendChild(i)
	}
}
/* eslint-disable no-new */
new Vue({
	store,
	router,
	render: h => h(App)
}).$mount('#app-box')



