
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Swiper,
		SwiperItem
	},
	computed: {
		...mapState({
			storeInfo: state => state.storex.storeInfo,
			url: state => state.storex.url
		})
	},
	data () {
		return {
		}
	},
	created () {
		const self = this
		self.$store.commit('hasTabbar', {
			tabbar: true
		})
		if (!this.$route.params.storeid && this.$route.params.storeid === 'undefined') {
			this.$router.push('/')
		}
	}
}



