
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Tab,
		TabItem
	},
	computed: {
		...mapState({
			name: state => state.storex.storeInfo.title,
			storeid: state => state.storex.storeInfo.id,
			version: state => state.storex.storeInfo.version,
			url: state => state.storex.url
		}),
		reserve: function() {
			return (/Reserve/.test(this.$route.path)) ? '1' : '0'
		}
	},
	methods: {
		tabselected: function(index, item) {
			this.fid = index
			this.selected = item
			const self = this
			self.$http.get(this.url + '&do=category&op=goods_list', {
					params: {
						id: self.$route.params.storeid,
						first_id: self.fid,
						reserve: self.reserve
					}
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message,
							onHide() {
								self.$router.push('/')
							}
						})
					} else {
						self.goods_list = response.message.list
						self.have_subclass = response.message.have_subclass
					}
				})
		}
	},
	data() {
		return {
			have_subclass: 1,
			selected: '',
			list: [],
			goods_list: [],
			fid: 0
		}
	},
	created() {
		const self = this
		self.$store.commit('hasTabbar', {
			tabbar: true
		})
		if (!this.$route.params.storeid && this.$route.params.storeid === 'undefined') {
			this.$router.push('/')
		}
		this.$http.get(self.url + '&do=category&op=category_list', {
				params: {
					id: this.$route.params.storeid,
					reserve: this.reserve
				}
			})
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message,
						onHide() {
							self.$router.push('/')
						}
					})
				} else {
					self.list = response.message
					self.fid = getObjFirst(self.list)
					self.selected = self.list[self.fid]
					self.$http.get(self.url + '&do=category&op=goods_list', {
							params: {
								id: self.$route.params.storeid,
								first_id: self.fid,
								reserve: self.reserve
							}
						})
						.then(function(response) {
							response = response.data.message
							if (response.errno) {
								self.$vux.alert.show({
									title: '出错啦',
									content: response.message,
									onHide() {
										self.$router.push('/')
									}
								})
							} else {
								self.goods_list = response.message.list
								self.have_subclass = response.message.have_subclass
							}
						})
				}
			})
	}
}

function getObjFirst(obj) {
	for (let i in obj) return i
}




