
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Alert,
		Group,
		Cell
	},
	computed: {
		...mapState({
			url: state => state.storex.url
		})
	},
	data() {
		return {
			list: ''
		}
	},
	created() {
		const self = this
		this.$http.get(this.url + '&do=usercenter&op=credits_record&credittype=credit2')
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.list = response.message
				}
			})
	}
}




