
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Scroller,
		Divider,
		Spinner,
		XButton
	},
	computed: {
		...mapState({
			storeInfo: state => state.storex.storeInfo,
			url: state => state.storex.url
		})
	},
	methods: {
		update: function() {
			const self = this
			this.page = this.page + 1
			this.$http.get(this.url + '&do=category&op=more_goods', {
					params: {
						id: this.$route.params.storeid,
						sub_id: this.$route.params.sub_id ? this.$route.params.sub_id : 0,
						page: this.page
					}
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.goods_list = self.goods_list.concat(response.message.list)
						self.status.pullupStatus = 'default'
						if (!response.message.isshow) {
							self.status.pullupStatus = 'disabled'
						}
					}
				})
		}
	},
	data() {
		return {
			storeid: this.$route.params.storeid,
			goods_list: [],
			page: 1,
			pullupConfigs: {
				content: '加载更多',
				pullUpHeight: 60,
				height: 40,
				autoRefresh: false,
				downContent: '释放加载',
				upContent: '上拉加载更多',
				loadingContent: '正在加载....',
				clsPrefix: 'xs-plugin-pullup-'
			},
			status: {
				pullupStatus: 'default'
			}
		}
	},
	created() {
		const self = this
		if (!this.$route.params.storeid || !this.$route.params.sub_id) {
			this.$router.push('/')
		}
		this.$http.get(this.url + '&do=category&op=more_goods', {
				params: {
					id: this.$route.params.storeid,
					sub_id: this.$route.params.sub_id
				}
			})
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.goods_list = response.message.list
					if (!response.message.isshow) {
						self.status.pullupStatus = 'disabled'
					}
				}
			})
	}
}




