
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Tab,
		TabItem,
		Divider
	},
	computed: {
		...mapState({
			name: state => state.storex.storeInfo.title,
			storeid: state => state.storex.storeInfo.id,
			version: state => state.storex.storeInfo.version,
			url: state => state.storex.url
		})
	},
	methods: {
		tabselected: function(index, item) {
			this.fid = index
			this.selected = item
			this.orderlist = index ? this.over : this.unfinish
		}
	},
	data() {
		return {
			selected: '未完成订单',
			fix: '0',
			list: ['未完成订单', '完成订单'],
			over: [],
			unfinish: [],
			orderlist: []
		}
	},
	created() {
		const self = this
		self.$store.commit('hasTabbar', {
			tabbar: true
		})
		this.$http.get(this.url + '&do=orders&op=order_list')
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message,
						onHide() {
							self.$router.push('/')
						}
					})
				} else {
					self.over = response.message.over
					self.orderlist = self.unfinish = response.message.unfinish
				}
			})
	}
}




