
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Alert,
		Group,
		Cell
	},
	data () {
		return {
			avatar: 'http://we7cloud-10016060.file.myqcloud.com/images/2016/07/16/jKKTsiIS08blbi6L5789f678486ee.jpg',
			nickname: '呵呵',
			member: 0,
			credit1: 0,
			credit2: 0
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.storex.userInfo
		})
	},
	beforeCreate () {
		const self = this
		self.$store.commit('hasTabbar', {
			tabbar: true
		})
	}
}



