
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Radio,
		Popup,
		Group,
		XSwitch,
		XInput,
		XButton,
		Scroller,
		Toast,
		XAddress,
		ChinaAddressData,
		Alert,
		Cell
	},
	data() {
		return {
			sex: [{
				key: '1',
				value: '男'
			}, {
				key: '2',
				value: '女'
			}],
			nicknameedit: '',
			realnameedit: this.realname,
			showedit: false,
			editname: '',
			gender: 1,
			genderedit: this.gender
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.storex.userInfo,
			url: state => state.storex.url
		})
	},
	methods: {
		editshow: function(name) {
			this.showedit = true
			this.editname = name
		},
		update(value) {
			let fields = {}
			const self = this
			switch (this.editname) {
				case 'nickname':
					fields.nickname = this.nicknameedit
					break
				case 'realname':
					fields.realname = this.realnameedit
					break
				case 'gender':
					fields.gender = value
					break
			}
			this.$http({
					method: 'post',
					url: this.url + '&do=usercenter&op=personal_update',
					data: { fields }
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.toast.show({
							text: response.message,
							type: 'warn'
						})
					} else {
						self.$vux.toast.show({
							text: '修改成功',
							type: 'success'
						})
						self.$http.get(self.url + '&do=usercenter&op=personal_info')
							.then(function(response) {
								response = response.data.message
								if (response.errno) {
									self.$vux.alert.show({
										title: '出错啦',
										content: response.message
									})
								} else {
									self.$store.commit('setUserInfo', response.message)
								}
							})
						self.showedit = false
					}
				})
		}
	},
	filters: {
		sex: function(value) {
			if (!value) return ''
			const sex = ['保密', '男', '女']
			return sex[value]
		}
	},
	created() {
		this.nicknameedit = this.userInfo.nickname
		this.realnameedit = this.userInfo.realname
		this.genderedit = this.userInfo.gender
	}
}




