
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Group,
		Cell,
		XButton,
		Flexbox,
		FlexboxItem,
		Swipeout,
		SwipeoutItem,
		SwipeoutButton
	},
	computed: {
		...mapState({
			url: state => state.storex.url
		})
	},
	data() {
		return {
			addresslists: [],
			auto: false
		}
	},
	methods: {
		del: function(id) {
			const self = this
			this.$http.get(this.url + '&do=usercenter&op=address_delete', {
					params: {
						id: id
					}
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.$vux.toast.show({
							text: '删除成功',
							onShow() {
								self.$http.get(self.url + '&do=usercenter&op=address_lists')
									.then(function(response) {
										response = response.data.message
										if (response.errno) {
											self.$vux.alert.show({
												title: '出错啦',
												content: response.message
											})
										} else {
											self.addresslists = response.message
										}
									})
							}
						})
					}
				})
		},
		def: function(id) {
			const self = this
			this.$http.post(this.url + '&do=usercenter&op=address_default&id=' + id, {
					fields: {
						isdefault: 1
					}
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.$vux.toast.show({
							text: '修改成功',
							onShow() {
								self.$http.get(self.url + '&do=usercenter&op=address_lists')
									.then(function(response) {
										response = response.data.message
										if (response.errno) {
											self.$vux.alert.show({
												title: '出错啦',
												content: response.message
											})
										} else {
											self.addresslists = response.message
										}
									})
							}
						})
					}
				})
		}
	},
	created() {
		const self = this
		this.$http.get(this.url + '&do=usercenter&op=address_lists')
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.addresslists = response.message
				}
			})
	}
}




