
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Search,
		Group,
		Cell
	},
	computed: {
		...mapState({
			url: state => state.storex.url
		})
	},
	data() {
		return {
			results: [],
			value: '',
			list: ''
		}
	},
	created() {
		const self = this
		this.$http.get(self.url + '&do=store&op=store_list')
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					if (response.message.version !== '1') {
						self.$router.push('/StoreIndex/' + response.message.stores[0]['id'])
					} else {
						self.list = response.message.stores
					}
				}
			})
	}
}




