
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Group,
		Cell,
		XButton,
		Flexbox,
		FlexboxItem
	},
	computed: {
		...mapState({
			url: state => state.storex.url
		})
	},
	data() {
		return {
			orderinfo: [],
			storeinfo: []
		}
	},
	created() {
		const self = this
		this.$http.get(this.url + '&do=orders&op=order_detail', {
				params: {
					id: this.$route.params.oid
				}
			})
			.then(function(response) {
				response = response.data.message
				if (response.errno) {
					self.$vux.alert.show({
						title: '出错啦',
						content: response.message
					})
				} else {
					self.orderinfo = response.message
					self.storeinfo = response.message.store_info
					console.log(self.storeinfo.title)
				}
			})
	}
}




