
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import name2value from 'vux/src/filters/name2value.js'

import { mapState } from 'vuex'
export default {
	components: {
		Group,
		Cell,
		XInput,
		XAddress,
		XTextarea
	},
	computed: {
		...mapState({
			url: state => state.storex.url
		}),
		addressValue: {
			get: function() {
				if (this.province && this.city && this.district) {
					return [this.province, this.city, this.district]
				} else {
					return []
				}
			},
			// setter
			set: function(newValue) {
				newValue = value2name(newValue, this.addressData)
					.split(' ')
				this.address.province = newValue[0]
				this.address.city = newValue[1]
				this.address.district = newValue[2]
			}
		}
	},
	methods: {
		changeData() {
			const addressinfo = [this.address.province, this.address.city, this.address.district]
			const newValue = name2value(addressinfo, this.addressData).split(' ')
			this.province = newValue[0]
			this.city = newValue[1]
			this.district = newValue[2]
		},
		post: function() {
			const self = this
			const id = (self.$route.params.id === 'new') ? '' : self.$route.params.id
			this.$http.post(this.url + '&do=usercenter&op=address_post&id=' + id, {
					fields: this.address
				})
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.$vux.toast.show({
							text: '修改成功'
						})
						self.$router.push('/Home/Address')
					}
				})
		}
	},
	data() {
		return {
			id: '',
			addressData: ChinaAddressData,
			province: '',
			city: '',
			district: '',
			address: {
				username: '',
				province: '山西省',
				city: '太原市',
				district: '小店区',
				mobile: '',
				address: '',
				zipcode: ''
			}
		}
	},
	created() {
		if (this.$route.params.id && this.$route.params.id !== 'new') {
			const self = this
			this.$http.get(this.url + '&do=usercenter&op=current_address&id=' + self.$route.params.id)
				.then(function(response) {
					response = response.data.message
					if (response.errno) {
						self.$vux.alert.show({
							title: '出错啦',
							content: response.message
						})
					} else {
						self.address = response.message
						self.changeData()
					}
				})
		}
	}
}




